//STYLE AND FONT VARIABLES

$buttonBackgroundColor: red;
$bodyFont: "Trebuchet MS", "Lucida Sans Unicode", "Lucida Grande", "Lucida Sans",
  Arial, sans-serif;
$bodybackgroundColor: #fafafa;
$addToCart: #fafafa;
$bodyColor: rgb(0, 0, 0);
$headerLinks: #a00b0b;


html {
  font-size: 62.5%;
}

* {
  padding: 0;
  margin: 0;
}

body {
  font-family: serif;
  font-size: 2rem;
  letter-spacing: 400;
  font-weight: 400;
  line-height: 3.2rem;
  color: $bodyColor;
}

ul {
  list-style: none;
}

img {
  width: 100%;
  height: auto;
  display: block;
}


.wrapper {
  max-width: 1200px;
  width: 90%;
  margin: 0 auto;
}

.siteHeaderWrapper {
max-width: 1500px;
  width: 100%;
  margin: 0 auto;
   display: flex;
  justify-content: space-between;

}

.fullWidthWrapper {
  max-width: 1700px;
  width: 95%;
  margin: 0 auto;
}

.backtoTop {
  position: fixed;
  bottom: 55px;
  right: 6.5%;
  font-size: 3rem;
  color: $bodyColor;
  text-decoration: none;
  padding: 20px;
  background-color: rgba(169, 169, 169, 0.205);
  border-radius: 50%;
  -webkit-box-shadow: inset -2px 3px 40px 7px #afafaf;
  box-shadow: inset -2px 3px 40px 7px #afafaf;
  border: 2px solid #afafaf02;
}

.errorMessage {
  height: 300px;
  padding: 100px;
}

/* SKIP LINK  */
.skipLink {
  position: absolute;
  left: -3000px;
  top: 10px;
  z-index: 999;
  background-color: #ffffff;
  color: #1f2022;
}

.skipLink:focus {
  left: 0;
}

.backtoTop:focus,
.backtoTop:hover {
  border: 2px solid rgb(59, 59, 59);
}


@media (max-width: 650px) {
  .backtoTop {
    font-size: 2.5rem;
    padding: 10px;
    bottom: 20px;
    right: 4%;
  }
}
